@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);


body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{
	font-family: 'Open Sans', sans-serif;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root{
  --text: white;
  --offWhite: #ffffff7d;
  --highlight: gold;
  --offGold: #ffd7007d;
  --bg:#262626;
  --navclr:#3b3b3b
}

html {
  overflow-y: scroll;
}

body{
  background-color: #262626 !important;
  background-color: var(--bg) !important;
}

.main{
  display:flex;
}

.btn{
  border-radius: 0px !important;
}

.container{
  max-width: 100% !important;
}

p{
  padding: 1rem;
}

.navbar{
    padding-top: 0rem !important;
    width: 100%
}

.nav{
    width: 100%;
    justify-content: space-between;
    background-color: #3b3b3b;
    box-shadow: 0px 0px 5px black;
    padding-top: .5rem;
    padding-bottom: .5rem;
}

.inline-flex{
    display: inline-flex;
    padding-left: 2rem;
    padding-right: 2rem;
}

.inline-flex-block{
    display: inline-flex;
    padding-left: 2rem;
    padding-right: 2rem;
}

.nav-link{
    color: var(--text) !important;
    margin-left:1.5rem;
}

.margin-left{
    margin-left: .75rem;
}

.nav-link:hover{
    color:var(--highlight) !important;
}

.nav-link:focus{
    color:var(--highlight) !important;
}

.nav-link:focus-visible{
    border: 0px var(--bg) solid;
}

@media screen and (max-width: 768px) {
    .inline-flex-block {
      display: block;
    }
  }


.jumbotron{
    margin-top: 3rem;
    color: var(--text);
    display:flex;
    justify-content: center;
}

.btn-primary{
    margin-right: .5rem;
    color: var(--bg);
    background-color: var(--highlight);
    border: 1px solid var(--highlight);
    width:49%;
}

.btn-primary:hover{
    color: var(--highlight) !important;
    background-color: var(--bg);
    border-color: var(--offGold)
}

.btn-primary:focus{
    color: var(--highlight) !important;
    background-color: var(--bg);
    border-color: var(--highlight);
    box-shadow: none !important;
}

.btn-container{
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}
.linkContainer{
    display: flex;
    justify-content: space-around;
}

.cardContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.card{
    width: 100%;
    height: 100%;
    color: var(--text);
    margin: 2rem;
    border-radius: 0%;
    border: none;
    background-color: inherit;
}

.card-img-top{
    border-radius: 0%;
    height: 140px;
}

.visit{
    color:red;
    position :absolute;
    left: 100px;
    top:70px;
}

.imgLink:hover{
    opacity: 75%;
}

.btn-secondary{
    color: var(--text);
    background-color: var(--bg);
    border-color: var(--offWhite);
}

.btn-secondary:hover{
    color: var(--highlight);
    background-color: var(--bg);
    border-color: var(--offGold);
}

.btn-secondary:focus{
    box-shadow: none !important;
    border-color: var(--highlight);
}
.row{
    color: var(--text)
}

.card-title{
    color: var(--highlight);
}

.no-margin{
    margin: 0px;
}
.modal-open{
    padding-right: 0px !important;
}

.modal-content{
    background-color: var(--bg) !important;
    color: var(--text);
    border-radius: 0px;
    border: black 0px solid;
}

.modal-header{
    border-color: var(--navclr)
}

.modal-body{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.info-card{
    display: block;
    width: 50%;
    padding: .5rem;
    flex-wrap: wrap;
}

.flex-centered{
    display:flex;
    justify-content: center;
    box-sizing: border-box;
}

.info-style{
    color: var(--text);
    margin-bottom: .25rem;
}

.info-card:hover .info-style{
    color: var(--highlight)
}

a{
    text-decoration: none !important;
}

